    export const catA={
        catHeader:['Cz A','Rozumienie ze suchu'],
        subCat:[
            {
                headQuiz:['Zadania z jednostkami wyboru wielokrotnego','Taki typ wiczenia moe mie rne formy. Jedn z nich jest zadanie, ktre odtwarzane jest tylko jeden raz. Zdajcy wysuchuje krtkiego komunikatu, a nastpnie musi zdecydowa, co oznacza wypowied lub gdzie jest moliwa do usyszenia. Warto przeczyta wszystkie odpowiedzi przed usyszeniem komunikatu. Wybiera si spord trzech moliwoci: jedna z nich jest nieprawdopodobna, druga moliwa, a tylko jedna prawidowa.'],
                component:<SubCatA1/>,
                quizes:[QuizA1_1, QuizA1_2, QuizA1_3, QuizA1_4, QuizA1_5, QuizA1_6, QuizA1_7, QuizA1_8, QuizA1_9] },
            {
                headQuiz:['Zadania z wyborem wielokrotnym','Inn form zadania z wyborem wielokrotnym s dusze wypowiedzi jednej lub kilku osb. Przed wysuchaniem nagrania naley przeczyta tylko pierwsze czci zda i pomin warianty odpowiedzi, aby nie wprowadzay w bd'],
                component:<SubCatA1></SubCatA1>,
                quizes:[QuizA2_1, QuizA2_2, QuizA2_3, QuizA2_4, QuizA2_5] 
                // quizes:[QuizA2_1,QuizA2_2] 
            },
            {
                headQuiz:['Zadania z lukami do uzupenienia',`Zadanie polega na uzupenieniu usyszanymi wyrazami luk w tekcie, ktry zdajcy
                w niepenej wersji ma przed sob. Przed wysuchaniem nagrania warto si zastanowi,
                czego brakuje [np. rzeczownika, czasownika], a take z jakim tematem mog by zwizane
                wyrazy, ktre naley wpisa.`],
                component:<SubCatA3></SubCatA3>,
                quizes:[QuizA3_1, QuizA3_2, QuizA3_3, QuizA3_4, QuizA3_5, QuizA3_6, QuizA3_7, QuizA3_8, QuizA3_9] 
            },
            {
                headQuiz:['Zadania z jednostkami prawda/fasz (P/F)',`Przed wysuchaniem nagrania naley przeczyta tekst zadania i podkreli sowa kluczowe
                (np. imiona, nazwiska, miejsca czy daty), co pomoe zaznaczy dobre odpowiedzi.
                Naley wykonywa zadanie ju w trakcie suchania, a po zakoczeniu nagrania
                przyjrze si swoim odpowiedziom.`],
                component:<SubCatA4></SubCatA4>,
                quizes:[QuizA4_1, QuizA4_2, QuizA4_3, QuizA4_4, QuizA4_5, QuizA4_6, QuizA4_7, QuizA4_8, QuizA4_9, QuizA4_10, QuizA4_11, QuizA4_12] 
            },
            {
                headQuiz:['Zadania z jednostkamina dobieranie informacji',`To zadanie zwykle polega na dopasowaniu do wypowiedzi kilku osb odpowiednich informacji. Takie zadanie odrnia si graficznie od pozostaych, poniewa zwykle podzielone jest na dwie kolumny. Najczciej naley wpisa odpowiednie litery lub numery przy kolejnych wypowiedziach. Dlatego warto wczeniej uwanie przeczyta zadanie i zorientowa si w jego ukadzie. Dobrze jest podkreli sowa klucze, okreli tematyke zadania.`],
                component:<SubCatA5></SubCatA5>,
                quizes:[QuizA5_1, QuizA5_2, QuizA5_3, QuizA5_4, QuizA5_5, QuizA5_6] 
            },
            {
                headQuiz:['Zadania z wykorzystaniem materiaw graficznych',`Przed przystpieniem do tego zadania naley uwanie przyjrze si wszystkim fotografiom. Zwykle naley wpisa kolejno, w ktrej byy opisywane fotografie; czasami zdj jest wicej ni wypowiedzi. Innym razem naley zaznaczy fotografi, ktra obrazuje tre wypowiedzi. Wtedy najczciej naley wybra jedn spord trzech moliwoci.`],
                component:<SubCatA6></SubCatA6>,
                quizes:[QuizA6_1, QuizA6_2, QuizA6_3, QuizA6_4, QuizA6_5, QuizA6_6] 
            },
            {
                headQuiz:['Zadania z wykorzystaniem materiaw graficznych',`Przed przystpieniem do tego zadania naley uwanie przyjrze si wszystkim fotografiom. Zwykle naley wpisa kolejno, w ktrej byy opisywane fotografie; czasami zdj jest wicej ni wypowiedzi. Innym razem naley zaznaczy fotografi, ktra obrazuje tre wypowiedzi. Wtedy najczciej naley wybra jedn spord trzech moliwoci.`],
                component:<SubCatA7></SubCatA7>,
                quizes:[QuizA7_1] 
            }
        ]
    }

   export const catB={
        catHeader:['Cz B','Poprawno gramatyczna'],
        subCat:[
            {
                headQuiz:['Transformacje',`To najczciej spotykany typ zadania. W nawiasie podane s formy podstawowe rzeczownikw, czasownikw, zaimkw, przymiotnikw, liczebnikw, przyswkw naley utworzy poprawn form tych wyrazw.`],
                component:<SubCatB1></SubCatB1>,
                quizes:[QuizB1_1, QuizB1_2, QuizB1_3, QuizB1_4, QuizB1_5, QuizB1_6, QuizB1_7, QuizB1_8, QuizB1_9, QuizB1_10, QuizB1_11, QuizB1_12, QuizB1_13, QuizB1_14, QuizB1_15, QuizB1_16, QuizB1_17, QuizB1_18, QuizB1_19, QuizB1_20, QuizB1_21, QuizB1_22] 
            },
            {
                headQuiz:['Transformacje',`Tym typem transformacji jest przeksztacenie zda przy uyciu podanych wyrazw. W poleceniu znajduje si wzr, wedug ktrego powinno si przeksztaca zdania, np. wszystkie transformacje naley utworzy w formie trybu przypuszczajcego. Trzeba pamita o dodatkowych operacjach, np. zamianie czasownika na rzeczownik, zamianie rzeczownikw na odpowiednie zaimki.`],
                component:<SubCatB2></SubCatB2>,
                quizes:[QuizB2_1, QuizB2_2, QuizB2_3] 
            },
            {
                headQuiz:['Zadania z lukami',`Takie zadanie polega na uzupenieniu tekstu wyrazami, ktre znajduj si w ramce. Czasami wyrazw jest wicej ni luk w zadaniu, a niektre wyrazy mog by uyte wicej ni raz. Naley wybra odpowiedni wyraz i wpisa w puste miejsce.`],
                component:<SubCatB3></SubCatB3>,
                quizes:[QuizB3_1, QuizB3_2, QuizB3_3, QuizB3_4] 
            },
            {
                headQuiz:['Wybr wielokrotny',`W tym typie zadania naley wybra poprawn form spord kilku podanych. Jeeli potrafimy zidentyfikowa dobr form, moemy od razu j wybra i nie czyta pozostaych propozycji. Kiedy natomiast nie jestemy pewni, pomocna okaza si moe
                metoda eliminacji. Naley rozpocz od wyeliminowania formy najmniej prawdopodobnej.`],
                component:<SubCatB4></SubCatB4>,
                quizes:[QuizB4_1, QuizB4_2] 
            }
        ]
    }

